@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.card {
  background-color: #fff;
  border-radius: 10px;
  height: 400px;
  width: 231px;
  padding: 20px;
  border: solid 1px #ddd;
  margin-bottom: 20px; }

.img-wrapper {
  max-width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%; } }

.card-body {
  font-family: 'Roboto', sans-serif;
  padding: 5px 5px 8px;
  flex: none;
  height: 90px;
  cursor: pointer;
  a {
    font-size: 17px;
    text-decoration: none;
    color: $dark-color;
    font-weight: 400; }
  &:hover a {
    text-decoration: underline;
    text-decoration-color: $dark-color; } }

.price {
  font-size: 18px;
  font-weight: 400;
  color: $dark-color;
  height: 32px;
  padding: 0 5px;
  margin-bottom: 8px;
  cursor: default;
  display: block; }

.card-info {
  padding-top: 14px;
  display: inline-block;
  border-top: solid 1px #ddd; }

.isExist {
  font-size: 14px;
  line-height: 1.5;
  float: left;
  color: $main-color;
  margin-top: -2px;
  cursor: default;
  img {
    margin-bottom: 3px; }
  span {
    margin-left: 5px;
    display: inline-block; } }

.small-buy {
  float: right;
  background-color: $main-color;
  border-radius: 5px;
  height: 25px;
  width: 80px;
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
  color: #fff;
  margin-top: -3px;
  transition: all .3s ease;
  &:hover {
    background-color: #2DB32D; } }


.fav-check {
  width: 18px;
  height: 18px;
  display: block;
  background: url('../../assets/like.svg') no-repeat;
  background-position: 0 0;
  position: absolute;
  top: 10px;
  right: 10px; }

.fav-selected {
  background-position: -18px 0; }
