@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.shop-page {
  padding-top: 16px; }

.shop-content {
  background-color: #fff;
  border-radius: 8px;
  padding: 14px 20px;
  h2 {
    font-size: 24px;
    font-weight: 500;
    color: $dark-color;
    line-height: 1.5;
    font-family: 'Roboto', sans-serif;
    margin-left: 5px;
    margin-bottom: 20px; } }

.items-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
