@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

header {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  color: $dark-color; }

a {
  color: $dark-color;
  transition: all .4s ease;
  &:hover {
    text-decoration: none;
    color: $main-color; } }

.stretching {
  background-color: #2bc2d9;
  height: 30px;
  text-align: center;
  a {
    color: #fff;
    font-size: 16px;
    cursor: default;
    &:hover {
      color: #fff; } } }

.primary {
  background-color: #fff;
  .inner-primary {
    max-width: 1440px;
    width: 100%;
    height: 65px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    align-items: center;
    position: relative;
    .logo {
      color: #fff;
      background-color: $main-color;
      width: 230px;
      height: 35px;
      margin-left: 28px;
      margin-right: 20px;
      text-align: center;
      border-radius: 5px;
      vertical-align: middle;
      line-height: 2;
      cursor: default; }
    ul {
      margin: 0;
      margin-left: auto;
      margin-right: 60px;
      li {
        display: inline-block;
        font-weight: 500;
        margin: 0 20px;
        line-height: 1.8; } }
    .contacts {
      font-size: 19px;
      line-height: 1.8;
      font-weight: 700;
      margin-right: 28px;
      a:hover {
        color: $dark-color;
        cursor: default; } } } }

.secondary {
  background-color: #777;
  .secondary-inner {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    height: 55px;
    display: flex;
    color: #fff;
    align-items: center; } }

.categories-header {
  background-color: #666;
  height: 55px;
  display: flex;
  align-items: center;
  padding: 0 28px;
  font-size: 19px;
  cursor: default;
  span {
    width: 24px;
    height: 1px;
    background-color: #fff;
    position: relative;
    margin-right: 10px;
    margin-bottom: 1px;
    &:before {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background-color: #fff;
      top: 6px; }
    &:after {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background-color: #fff;
      bottom: 6px; } } }

.favs-and-cart {
  justify-content: center;
  display: flex;
  .favs {
    background: url('../../assets/heart.svg') no-repeat;
    width: 32px;
    height: 32px;
    display: block;
    margin-left: 35px;
    margin-right: 20px;
    cursor: pointer; }
  .cart {
    background: url('../../assets/shopping-cart.svg') no-repeat;
    width: 32px;
    height: 32px;
    display: block;
    cursor: pointer; } }

.btn-clear {
  border: none;
  &:hover {
    border: none; } }
